import Fetch from 'isomorphic-unfetch';

class WeatherMap extends React.Component{

  state = {}

  componentDidMount () {
      console.log('in component did mount');
  }


  getWeatherData = () => {
    let data = this.props;
    console.log(data);
    return data.toString();
  }

  render(){
    return(
      <div>
        {this.getWeatherData()}
        <img src={this.state} alt="Logo" />
      </div>
    );
  }
}

export default WeatherMap;
